import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import addNotice from './addForm.vue';
import { deepClone } from '@/until/index';
let noticeConfig = class noticeConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '公告配置';
        this.showBtn = false;
        this.noticeList = deepClone(this.noticeConfig);
    }
    showPopup(refId) {
        this.maskClick();
        const component = this.$refs[refId];
        component.show();
    }
    hidePopup(refId) {
        const component = this.$refs[refId];
        component.hide();
    }
    get noticeConfig() {
        return this.$store.state.schoolInfo.noticeConfig || [];
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    maskClick() {
        this.$refs.addNotice.reset();
    }
    submit(model) {
        this.noticeList.push({
            title: model.title,
            url: ''
        });
        this.showBtn = true;
        this.hidePopup('myPopup');
    }
    deleteNotice(index) {
        this.noticeList.splice(index, 1);
        this.showBtn = true;
    }
    saveSchoolInfo() {
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: 'noticeConfig',
            value: this.noticeList
        }).then((e) => {
            if (e.success) {
                this.showBtn = false;
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: 'noticeConfig',
                value: this.noticeList
            });
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
};
noticeConfig = __decorate([
    Component({
        components: {
            page,
            pageView,
            addNotice
        }
    })
], noticeConfig);
export default noticeConfig;
